*{
  margin:0;
  padding:0;
}

html,body{
  font-size:18px;
  background-color: #ac7110;

}

h1{
 
  font-size:2.5em;
  font-family:Lato, sans-serif;
  font-weight:700;
  line-height: 1.1;
  margin: 40px 0;
  color: white;
}

.App{
  min-height: 100vh;/*altura del navegador*/
  display:flex;/*definir la ubicacion dentro del contenedor*/
  flex-wrap: wrap;/* si un elemento toma todo el ancho de contenedor, los otros elementos se ubicaran debajo o en la siguiente liena*/
  align-items: center;
  justify-content: center;
  text-align:center;
}

.contenedor-principal{
  min-height: 600px;/*altura minima*/
  display:flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

}