.contenedor-ligas{
    width: auto;  
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:#711900;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
}
.imagen-liga{
    height: 100%;
    width: auto;
}

.contenedor-texto-liga{
    text-align:center;
    padding:40px;
    width: 500px;
    font-family:Lato,sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
    color: white;
} 

.nombre-liga{
    font-size:1.3rem;
}
.deporte-liga{
    font-size:1.1rem;
    margin-top:15px;
    padding-bottom: 30px;
    
}
.representante-liga{
    font-size: 1.1rem;
    text-align:center;
    
}
.contacto-liga{
    font-size:1.1rem;
}

.h2,h3,b{
  color: #ac7110;

}